import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message, Checkbox } from 'antd';
import { getPracticeTypes, deletePracticeType } from 'services/practiceType';

class TrainingTypePage extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deletePracticeType(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'K hiu',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tn loi RL',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'im ti a',
        dataIndex: 'grade',
        key: 'grade',
      },
      {
        title: 'Tnh tng im',
        dataIndex: 'isTotal',
        key: 'isTotal',
        render: value => <Checkbox checked={value} />,
      },
      {
        title: 'im hc tp',
        dataIndex: 'isStudy',
        key: 'isStudy',
        render: value => <Checkbox checked={value} />,
      },
      {
        title: 'Hin th',
        dataIndex: 'isShown',
        key: 'isShown',
        render: value => <Checkbox checked={value} />,
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.REN_LUYEN__LOAI_REN_LUYEN__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Loi rn luyn">
        <CartHeadAction>
          <Link to={routeConstant.REN_LUYEN__LOAI_REN_LUYEN__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getPracticeTypes}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TrainingTypePage />
  </Page>
);



